import React, {useEffect, useState} from 'react';
import ReactDOM from 'react-dom';
import styles from './Tabs.module.css'
import UserPass from '../UserPass'

function Tabs(props) {
	const {onClickLogin1, onClickLogin2, onClickLogin3} = props
	
	const initialiseState = () => {return {showMediaPlannerLogin: true, showMediaOwnerLogin:false, showAdminLogin:false, tablinks1:styles.tablinksa, tablinks2:styles.tablinks, tablinks3:styles.tablinks}}
	
	let [componentStates, setState] = useState(initialiseState)
	
	useEffect(()=>{
				//setState(p => ({...p, node: chart3(mysvg), mysvg}))
			},[])

	const makeVisible = (evt) => {
		switch(evt)
		{
			case 'planner': setState((p)=>({showMediaPlannerLogin: true, showMediaOwnerLogin:false, showAdminLogin:false, 	tablinks1:styles.tablinksa, tablinks2:styles.tablinks, tablinks3:styles.tablinks})); break
			case 'owner': setState((p)=>({showMediaPlannerLogin: false, showMediaOwnerLogin:true, showAdminLogin:false, 	tablinks1:styles.tablinks, tablinks2:styles.tablinksa, tablinks3:styles.tablinks})); break
			case 'admin': setState((p)=>({showMediaPlannerLogin: false, showMediaOwnerLogin:false, showAdminLogin:true, 	tablinks1:styles.tablinks, tablinks2:styles.tablinks, tablinks3:styles.tablinksa})); break
		}
	}


	return (
	<div className={styles.tabscontainer}>
		<div className={styles.tabs}>
			<div className={styles.tab}>
				<button className={componentStates.tablinks1} onClick={()=>makeVisible('planner')}>Media Planner Login</button>
				<button className={componentStates.tablinks2} onClick={()=>makeVisible('owner')}>Media Owner Login</button>
				<button className={componentStates.tablinks3} onClick={()=>makeVisible('admin')}>Admin Login</button>
			</div>

			{componentStates.showMediaPlannerLogin && 
				<div className={styles.tabcontent}>
				  <h3>Media Planner Login</h3>
				  <UserPass onClickLogin={onClickLogin1}/>
				</div>
			}

			{componentStates.showMediaOwnerLogin && 
				<div className={styles.tabcontent}>
				  <h3>Media Owner Login</h3>
				  <UserPass onClickLogin={onClickLogin2}/>
				</div>
			}

			{componentStates.showAdminLogin && 
				<div className={styles.tabcontent}>
				  <h3>Admin Login</h3>
				  <UserPass onClickLogin={onClickLogin3}/>
				</div>
			}
		</div>
	</div>
	)
}

export default Tabs