import React, {useEffect, useState} from 'react';
import ReactDOM from 'react-dom';
import styles from './UserPass.module.css'

function UserPass(props) {
	
	let {onClickLogin } = props
	
	const initialiseState = () => {return {userName:''}}
	
	let [componentStates, setState] = useState(initialiseState)
	
	useEffect(()=>{
				//setState(p => ({...p, node: chart3(mysvg), mysvg}))
			},[])

	return (
	<div className={styles.userpasscontainer}>

		<div>
			<label htmlFor='username' className={styles.usernamelabel} >Username:</label>
			<input type='text' id='username' placeholder='type name & click login' onChange={(e)=>{
																							setState(p => (
																											{...p, userName:e.target.value}
																										)
																									)
																						}
																				}/>
		</div>

		<div>
			<label htmlFor='password' className={styles.passwordlabel}>Password:</label>
			<input type='password' id='password' disabled/>
		</div>
		
		<div>
			<button className={styles.loginbutton} onClick={()=>onClickLogin(componentStates.userName)}>Login</button>
		</div>
		

	</div>
	)
}

export default UserPass