import React, {useEffect, useState} from 'react';
import ReactDOM from 'react-dom';
import styles from './Login.module.css'
import Tabs from '../Tabs'
import Page1 from '../Page1'
import Page2 from '../Page2'
import Page3 from '../Page3'

export default function Login(props) {
	
	const initialiseState = () => {return {tabs: true, page1:false, page2:false, page3:false, userName:''}}
	
	let [componentState, setState] = useState(initialiseState)
	
	useEffect(()=>{
				//setState(p => ({...p, node: chart3(mysvg), mysvg}))
			},[])

	const onClickLogin1 = (userName) => {
		setState((p)=>({...p, tabs: false, page1:true, page2:false, page3:false, userName}))
	}

	const onClickLogin2 = (userName) => {
		setState((p)=>({...p, tabs: false, page1:false, page2:true, page3:false, userName}))
	}

	const onClickLogin3 = (userName) => {
		setState((p)=>({...p, tabs: false, page1:false, page2:false, page3:true, userName}))
	}

	return (
		<div className={styles.test}>
			{componentState.tabs && <Tabs onClickLogin1={onClickLogin1} onClickLogin2={onClickLogin2} onClickLogin3={onClickLogin3} />}
			{componentState.page1 && <Page1 userName={componentState.userName}/>}
			{componentState.page2 && <Page2 userName={componentState.userName}/>}
			{componentState.page3 && <Page3 userName={componentState.userName}/>}
		</div>
	)
}